import React, { useState } from 'react'
import { Asignatura } from './Asignatura'
import { Link, useHistory } from 'react-router-dom';


export const ListAsignaturas = () => {

    const [asignaturas, setAsignaturas] = useState(JSON.parse(localStorage.getItem("asignaturas")))

    return (
        <div>
            <h6>Lista de asignaturas</h6>
            {asignaturas ?
                asignaturas.map(function (object) {
                    return (
                        <div key={object.id} data={object}>
                            <Link to={
                                {
                                    pathname: '/asignatura/' + object.id + '/' + object.name,
                                }
                            } className="btn btn-success m-1">
                                {object.name}
                            </Link>
                        </div>
                    );
                }) : null
            }
        </div>
    )
}
