import React, { useState } from 'react'
import { ListAsignaturas } from './ListAsignaturas'

export const GlobalComponent = () => {

    const [asignaturas, setAsignaturas] = useState(JSON.parse(localStorage.getItem("asignaturas")));

    const [asignatura, setAsignatura] = useState("");

    const handleInputAsignatura = (e) => {
        setAsignatura(e.target.value);
    }

    const handleSubmitAsignatura = (e) => {

        e.preventDefault();

        if (!asignatura) {
            return;
        }

        if (!asignaturas) {
            let asignaturasTemp = [];
            asignaturasTemp.push({
                "id": 1, "name": asignatura
            })
            localStorage.setItem("asignaturas", JSON.stringify(asignaturasTemp));
        } else {

            let asignaturasLocal = JSON.parse(localStorage.getItem("asignaturas"));

            asignaturasLocal.push({
                "id": Number(asignaturas.length) + 1, "name": asignatura
            })

            localStorage.setItem("asignaturas", JSON.stringify(asignaturasLocal))
        }

        setAsignaturas(JSON.parse(localStorage.getItem("asignaturas")));

        setAsignatura("");

    }

    return (
        <div className="text-center mt-3 container">
            <h4>Study App</h4>
            <form className="form-group" onSubmit={handleSubmitAsignatura}>
                <p>Añadir asignatura</p>
                <input type="text" className="form-control w-auto mb-3 mr-auto ml-auto" value={asignatura} onChange={handleInputAsignatura} />
                <button className="btn btn-primary" onClick={handleSubmitAsignatura}>Añadir</button>
            </form>
            {asignaturas ? <ListAsignaturas key={asignaturas.length + 1} /> : null}
        </div>
    )
}
